@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.4.1/css/bootstrap.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.12.1/css/all.min.css);

/* For Bootstrap V 4.4.1 */


/* For Fontawesome  V 5.12.1*/

*{
    scroll-behavior: smooth;
}




/* For Custom Css V 1.0 */



.list_style_none{
    list-style: none;
}
/* Width */
.w_200{
    width: 12.5rem;
}
.w_250{
    width: 15.625rem;
}
.w_300{
    width: 18.75rem;
}

/* Height */
.h_100mvh{
    min-height: 100vh;
}

.h_200{
    height: 12.5rem;
}
.h_250{
    height: 15.625rem;
}
.h_300{
    height: 18.75rem;
}
@media (max-width: 768px){
    .header_sm_style{
        height: auto!important;
        width: 100%;
        position: static!important;
    }
    .header_sm_style #logo{
        width: 6rem!important;
        margin: 2px auto;
        height: 6rem;
        object-fit: cover;
        border-radius: 100%;
        display: none;
    }
    .text_sm_left{
        text-align: left!important;
    }
}

/* Padding and Margin */
.mp_0{
    margin: 0;
    padding: 0;
}
@media (min-width: 769px){
    .ml_250px{
        margin-left:250px
    }
}
/* Background bg */
.bg_222{
    background: #222;
}
.bg_bg_linear{
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.815)), to(rgba(0, 0, 2, 0.795)));
    background: linear-gradient(rgba(0, 0, 0, 0.815), rgba(0, 0, 2, 0.795));

}
.bg_none{
    background: none;
}
/* Border */
.border_228_bottom{
 border-bottom: 1px solid rgba(228, 222, 222, 0.103);
}
.border_bottom_dotted{
    border-bottom: 1px dotted white;
}



/* Font size */
.font_50per{
    font-size: .5rem;
}
.font_75per{
    font-size: .75rem;
}
.font_1r{
    font-size: 1rem;
}
.font_2r{
    font-size: 2rem;
}
.font_3r{
    font-size: 3rem;
}


/* Scroll Effect  */


::-webkit-scrollbar{
    width: 10px;
}
::-webkit-scrollbar-thumb{
    background: #444;
}


form *:focus{
    border: none;
    outline: none;
}
.text-light,
.text-light:active{
    color: white!important;
}


@-webkit-keyframes moveEffect{
    0%{
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
    100%{
        -webkit-transform: translateY(-200px);
                transform: translateY(-200px);
        opacity: 0;
        pointer-events: none;
    }
}


@keyframes moveEffect{
    0%{
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
    100%{
        -webkit-transform: translateY(-200px);
                transform: translateY(-200px);
        opacity: 0;
        pointer-events: none;
    }
}


.btn_toggle_bar{
    width: 50px;
    height: 40px;
    background: #444;
    border: none;
    outline: none;
   
}
.btn_toggle_bar:focus{
    outline: none;
}


.project_card .img_wrapper{
    height: 200px;
    position: relative;
    overflow: hidden;
}
.project_card .img_wrapper img{
    height: 100%;
    width: 100%;
    -webkit-transition: 1s;
    transition: 1s;
    object-fit: cover;
    object-position: left top;
}

.project_card .repo{
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
    padding: 5px;
    background: #333;
    color: white;
    width: 100%;
}
.settings{
    right: 5%;
    top: 10%;
    -webkit-animation: 5s linear infinite settings;
            animation: 5s linear infinite settings;
    cursor: pointer;
}
.settings img{
    box-shadow: 0 0 10px 2px inset deeppink;
    border-radius: 50%;
}
@-webkit-keyframes settings{
    0%{
        -webkit-transform: rotate(0turn);
                transform: rotate(0turn);
    }
    100%{
        -webkit-transform: rotate(1turn);
                transform: rotate(1turn);
    }
}
@keyframes settings{
    0%{
        -webkit-transform: rotate(0turn);
                transform: rotate(0turn);
    }
    100%{
        -webkit-transform: rotate(1turn);
                transform: rotate(1turn);
    }
}
.range_slider{
  position: relative;
  width: 100%;
  height: 10px;
  background: white;
  margin-bottom: .5rem;
}

.range_slider .range_value{
  position: absolute;
  right: -1.25rem;
  top: -2.5rem;
  width: 2.5rem;
  height: 2rem;
  background: #aaa;
  color: #222;
  text-align: center;
  line-height: 2rem;
  border-radius: 50%;
  color: blue;
  display: block;
}

.range_slider .range_scroll{
  position: absolute;
  height: 100%;
  left: 0;
  background: gray;

}
.range_slider .range_value::after{
  content: '';
  position: absolute;
  border: .8rem solid ;
  border-color: #aaa transparent transparent transparent ;
  top: 80%;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%)
}

.description > img{
  width: 35px;
  padding: 0 5px;
}
.PrivateTabIndicator-vertical-30{
    left: 0;
}
